export const httpAjax = Ajax;
/*_files:,js,css,less,isSequence */
export const loadFileList = (function() {
	/* ,*/
	let classcodes = [];
	/*JS,url:*/
	const loadFile = function(url) {
		if (!FileIsExt(classcodes, url)) {
			var ThisType = GetFileType(url);
			var fileObj = null;
			if (ThisType == ".js" || ThisType == "http") {
				fileObj = document.createElement("script");
				fileObj.src = url;
			} else if (ThisType == ".css") {
				fileObj = document.createElement("link");
				fileObj.href = url;
				fileObj.type = "text/css";
				fileObj.rel = "stylesheet";
			} else if (ThisType == ".less") {
				fileObj = document.createElement("link");
				fileObj.href = url;
				fileObj.type = "text/css";
				fileObj.rel = "stylesheet/less";
			}
			if (fileObj)
				return new Promise(function(resolve, reject) {
					fileObj.onload = fileObj.onreadystatechange = function() {
						if (!this.readyState || "loaded" === this.readyState || "complete" === this.readyState) {
							// success();
							classcodes.push(url);
							resolve();
						}
					};
					document.getElementsByTagName("head")[0].appendChild(fileObj);
				});
			else return Promise.resolve();
		} else {
			return Promise.resolve();
		}
	};
	/*,*/
	const GetFileType = function(url) {
		if (url != null && url.length > 0) {
			const lasindex = url.lastIndexOf(".");
			if (lasindex > -1) {
				return url.substr(url.lastIndexOf(".")).toLowerCase();
			} else if (/^(http:|https:)/.test(url)) {
				return "http";
			}
		}
		return "";
	};
	/**/
	const FileIsExt = function(FileArray, _url) {
		if (FileArray != null && FileArray.length > 0) {
			var len = FileArray.length;
			for (var i = 0; i < len; i++) {
				if (FileArray[i] == _url) {
					return true;
				}
			}
		}
		return false;
	};
	return function(_files, isSequence) {
		var FileArray = [];
		if (dataTypeTest(_files) === "array") {
			FileArray = _files;
		} else if (dataTypeTest(_files) === "string") {
			/*,*/
			FileArray = _files.split(",");
		}
		if (FileArray != null && FileArray.length > 0) {
			var LoadedCount = 0;
			if (isSequence) {
				//
				return new Promise(function(resolve, reject) {
					const len = FileArray.length;
					let P = loadFile(FileArray.shift());
					for (var i = 0; i < len - 1; i++) {
						const url = FileArray[i];
						P = P.then(function() {
							LoadedCount++;
							return loadFile(url);
						});
					}
					P.then(function() {
						LoadedCount++;
						if (LoadedCount === len) {
							resolve();
						}
					});
				});
			} else {
				//
				return new Promise(function(resolve, reject) {
					const len = FileArray.length;
					for (var i = 0; i < len; i++) {
						const url = FileArray[i];
						loadFile(url).then(function() {
							LoadedCount++;
							if (LoadedCount === len) {
								resolve();
							}
						});
					}
				});
			}
		} else {
			return Promise.reject();
		}
	};
})();
// import Vue from 'vue';

// const isServer = Vue.prototype.$isServer;
const isServer = false;
const SPECIAL_CHARS_REGEXP = /([\:\-\_]+(.))/g;
const MOZ_HACK_REGEXP = /^moz([A-Z])/;
const ieVersion = isServer ? 0 : Number(document.documentMode);

/*  */
const trim = function(string) {
	return (string || "").replace(/^[\s\uFEFF]+|[\s\uFEFF]+$/g, "");
};
/* istanbul ignore next */
const camelCase = function(name) {
	return name
		.replace(SPECIAL_CHARS_REGEXP, function(_, separator, letter, offset) {
			return offset ? letter.toUpperCase() : letter;
		})
		.replace(MOZ_HACK_REGEXP, "Moz$1");
};
//
const hasListenerEvents = [];
export const onNoRepeatEvent = function(element, event, handler) {
	let hasBanded = null;
	let index = 0;
	for (let i = 0; i < hasListenerEvents.length; i++) {
		const item = hasListenerEvents[i];
		if (item.element == element && item.event == event) {
			hasBanded = item;
			index = i;
			break;
		}
	}
	if (hasBanded) {
		off(hasBanded.element, hasBanded.event, hasBanded.handler);
		on(element, event, handler);
		hasListenerEvents.splice(index, 1, { element, event, handler });
	} else {
		on(element, event, handler);
		hasListenerEvents.push({ element, event, handler });
	}
};
/* on(element, event, handler) */
export const on = (function() {
	if (!isServer && document.addEventListener) {
		return function(element, event, handler) {
			if (element && event && handler) {
				element.addEventListener(event, handler, false);
			}
		};
	} else {
		return function(element, event, handler) {
			if (element && event && handler) {
				element.attachEvent("on" + event, handler);
			}
		};
	}
})();

/* off(element, event, handler) */
export const off = (function() {
	if (!isServer && document.removeEventListener) {
		return function(element, event, handler) {
			if (element && event) {
				element.removeEventListener(event, handler, false);
			}
		};
	} else {
		return function(element, event, handler) {
			if (element && event) {
				element.detachEvent("on" + event, handler);
			}
		};
	}
})();

/* one(element, event, handler) */
export const once = function(el, event, fn) {
	var listener = function() {
		if (fn) {
			fn.apply(this, arguments);
		}
		off(el, event, listener);
	};
	on(el, event, listener);
};

/*  */
export function hasClass(el, cls) {
	if (!el || !cls) return false;
	if (cls.indexOf(" ") !== -1) throw new Error("className should not contain space.");
	if (el.classList) {
		return el.classList.contains(cls);
	} else {
		return (" " + el.className + " ").indexOf(" " + cls + " ") > -1;
	}
}

/* className */
export function addClass(el, cls) {
	if (!el) return;
	var curClass = el.className;
	var classes = (cls || "").split(" ");

	for (var i = 0, j = classes.length; i < j; i++) {
		var clsName = classes[i];
		if (!clsName) continue;

		if (el.classList) {
			el.classList.add(clsName);
		} else {
			if (!hasClass(el, clsName)) {
				curClass += " " + clsName;
			}
		}
	}
	if (!el.classList) {
		el.className = curClass;
	}
}

/* className */
export function removeClass(el, cls) {
	if (!el || !cls) return;
	var classes = cls.split(" ");
	var curClass = " " + el.className + " ";

	for (var i = 0, j = classes.length; i < j; i++) {
		var clsName = classes[i];
		if (!clsName) continue;

		if (el.classList) {
			el.classList.remove(clsName);
		} else {
			if (hasClass(el, clsName)) {
				curClass = curClass.replace(" " + clsName + " ", " ");
			}
		}
	}
	if (!el.classList) {
		el.className = trim(curClass);
	}
}

/*  */
export const getStyle =
	ieVersion < 9
		? function(element, styleName) {
				if (isServer) return;
				if (!element || !styleName) return null;
				styleName = camelCase(styleName);
				if (styleName === "float") {
					styleName = "styleFloat";
				}
				try {
					switch (styleName) {
						case "opacity":
							try {
								return element.filters.item("alpha").opacity / 100;
							} catch (e) {
								return 1.0;
							}
						default:
							return element.style[styleName] || element.currentStyle
								? element.currentStyle[styleName]
								: null;
					}
				} catch (e) {
					return element.style[styleName];
				}
		  }
		: function(element, styleName) {
				if (isServer) return;
				if (!element || !styleName) return null;
				styleName = camelCase(styleName);
				if (styleName === "float") {
					styleName = "cssFloat";
				}
				try {
					var computed = document.defaultView.getComputedStyle(element, "");
					return element.style[styleName] || computed ? computed[styleName] : null;
				} catch (e) {
					return element.style[styleName];
				}
		  };

/*  style */
export function setStyle(element, styleName, value) {
	if (!element || !styleName) return;

	if (typeof styleName === "object") {
		for (var prop in styleName) {
			if (styleName.hasOwnProperty(prop)) {
				setStyle(element, prop, styleName[prop]);
			}
		}
	} else {
		styleName = camelCase(styleName);
		if (styleName === "opacity" && ieVersion < 9) {
			element.style.filter = isNaN(value) ? "" : "alpha(opacity=" + value * 100 + ")";
		} else {
			element.style[styleName] = value;
		}
	}
}
//
export const firstWordToUpperCase = function(str) {
	return str.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
};
const data_types = {
	array: "[object Array]",
	object: "[object Object]",
	string: "[object String]",
	number: "[object Number]",
	boolean: "[object Boolean]",
	null: "[object Null]",
	undefined: "[object Undefined]",
	function: "[object Function]",
	date: "[object Date]",
	symbol: "[object Symbol]",
	set: "[object Set]",
	Map: "[object Map]",
	regExp: "[object RegExp]",
	json: "[object JSON]", //JSON
	promise: "[object Promise]", //Promise
	element: /HTML\w+Element/g,
};
// isArray,isObject,isString,isPromise
export const dataType = {};
Object.keys(data_types).forEach((key) => {
	dataType["is" + firstWordToUpperCase(key)] = function(item) {
		return dataTypeTest(item) === key;
	};
});
// 
export const dataTypeTest = function(item) {
	const types = Object.keys(data_types);
	for (let index = 0; index < types.length; index++) {
		const key = types[index];
		const value = Object.prototype.toString.call(item);
		if (
			value === data_types[key] ||
			(Object.prototype.toString.call(data_types[key]) == "[object RegExp]" && data_types[key].test(value))
		) {
			return key;
		}
	}
};
//
export const deepCopyObject = function(obj) {
	if (!obj || dataTypeTest(obj) !== "object") return {};
	// return JSON.parse(JSON.stringify(obj));
	let newobj = {};
	for (var key in obj) {
		var type = dataTypeTest(obj[key]);
		switch (type) {
			case "string":
			case "null":
			case "number":
			case "undefined":
			case "function":
			case "boolean":
			case "symbol":
			case "set":
			case "map":
			case "json":
			case "promise":
			case "regExp":
			case "element":
				newobj[key] = obj[key];
				break;
			case "date":
				newobj[key] = obj[key].getTime();
				break;
			case "array":
				newobj[key] = deepCopyArray(obj[key]);
				break;
			case "object":
				newobj[key] = deepCopyObject(obj[key]);
				break;
		}
	}

	return newobj;
};
//
export const deepCopyArray = function(array) {
	if (!array || dataTypeTest(array) !== "array") return [];
	// return JSON.parse(JSON.stringify(array));
	var newarray = [];
	for (var i = 0; i < array.length; i++) {
		var item = array[i];
		var type = dataTypeTest(item);
		switch (type) {
			case "string":
			case "null":
			case "number":
			case "undefined":
			case "function":
			case "boolean":
			case "symbol":
			case "set":
			case "map":
			case "json":
			case "promise":
			case "regExp":
			case "element":
				newarray.push(item);
				break;
			case "date":
				newarray.push(item.getTime());
				break;
			case "array":
				newarray.push(deepCopyArray(item));
				break;
			case "object":
				newarray.push(deepCopyObject(item));
				break;
		}
	}
	return newarray;
};
///
export const deepCopy = function(value) {
	switch (dataTypeTest(value)) {
		case "object":
			return deepCopyObject(value);
		case "array":
			return deepCopyArray(value);
		default:
			return value;
	}
};
//
const arrayFilterFn = function(array, property) {
	const type = dataTypeTest(property);
	return array.filter((item) => {
		const itemtype = dataTypeTest(item);
		if (itemtype === "object" && type === "object") {
			let hasItem = false;
			Object.keys(property).forEach((el) => {
				hasItem = item[el] === property[el];
			});
			return hasItem;
		} else {
			return item === property;
		}
	});
};
//var arr=[{name:"",value:1},{name:"",value:2}] ,arrayFilterBy(arr,{value:2})[{name:"",value:2}]
//var arr=[1,2,3,2,4,3,5,3]   arrayFilterBy(arr,3) [3,3,3]
//var arr=[{name:"",value:1},{name:"",value:2},{name:"",value:4}] ,arrayFilterBy(arr,[{value:2},{value:4}])[{name:"",value:2},{name:"",value:4}]
//var arr=[1,2,3,2,4,3,5,3]   arrayFilterBy(arr,[3,2]) [3,3,3,2,2]
export const arrayFilterBy = function(array, property) {
	//property{name:'zgt'}'zgt'
	if (!array || dataTypeTest(array) !== "array") return [];
	const type = dataTypeTest(property);
	if (type === "array") {
		let newarray = [];
		property.forEach((item) => {
			newarray = newarray.concat(arrayFilterFn(array, item));
		});
		return newarray;
	} else {
		return arrayFilterFn(array, property);
	}
};
// yyyy-MM-dd HH:mm:ss
// yyyy-mm-dd yyyy/mm/ddyyyy-mm-ddyyyy-mm-ddyyyy/mm/dd
export const formatDate = function(time, format) {
	if (!time) return "";
	let date = dataTypeTest(time) === "date" ? time : new Date(time);
	let ft = trim(format || "yyyy-MM-dd HH:mm:ss").split(" ");
	let newdate = "";
	let symbol = "-";
	if (!ft[0]) return;
	if (/[-]+/.test(ft[0]) && !/[/]+/.test(ft[0])) {
		symbol = "-";
	} else if (!/[-]+/.test(ft[0]) && /[/]+/.test(ft[0])) {
		symbol = "/";
	} else if (/[:]+/.test(ft[0])) {
		symbol = ":";
	}
	let addZero = function(value) {
		if (Number(value) < 10) {
			return "0" + value;
		} else {
			return value;
		}
	};
	let setTimes = function(fts, symbol) {
		let newTime = "";
		let timeFtsLen = fts.split(":").length;
		switch (timeFtsLen) {
			case 1:
				newTime += addZero(date.getHours());
				break;
			case 2:
				newTime += addZero(date.getHours()) + symbol + addZero(date.getMinutes());
				break;
			default:
				newTime +=
					addZero(date.getHours()) +
					symbol +
					addZero(date.getMinutes()) +
					symbol +
					addZero(date.getSeconds());
				break;
		}
		return newTime;
	};

	if (symbol === ":") {
		newdate += setTimes(ft[0], symbol);
	} else {
		let timeFtsLen = ft[0].split(symbol).length;
		switch (timeFtsLen) {
			case 1:
				newdate += date.getFullYear();
				break;
			case 2:
				newdate += date.getFullYear() + symbol + addZero(date.getMonth() + 1);
				break;
			default:
				newdate +=
					date.getFullYear() + symbol + addZero(date.getMonth() + 1) + symbol + addZero(date.getDate());
				break;
		}

		if (ft[1]) {
			symbol = ":";
			newdate += " ";
			newdate += setTimes(ft[1], symbol);
		}
	}
	return newdate;
};

