function accMul(arg1, arg2) {
	let m = 0;
	const s1 = arg1.toString();
	const s2 = arg2.toString();
	m += s1.split(".").length > 1 ? s1.split(".")[1].length : 0;
	m += s2.split(".").length > 1 ? s2.split(".")[1].length : 0;
	return (Number(s1.replace(".", "")) * Number(s2.replace(".", ""))) / 10 ** m;
}

export function digitUppercase(n) {
	const fraction = ["", ""];
	const digit = ["", "", "", "", "", "", "", "", "", ""];
	const unit = [["", "", ""], ["", "", "", "", ""]];
	let num = Math.abs(n);
	let s = "";
	fraction.forEach((item, index) => {
		s += (digit[Math.floor(accMul(num, 10 * 10 ** index)) % 10] + item).replace(/./, "");
	});
	s = s || "";
	num = Math.floor(num);
	for (let i = 0; i < unit[0].length && num > 0; i += 1) {
		let p = "";
		for (let j = 0; j < unit[1].length && num > 0; j += 1) {
			p = digit[num % 10] + unit[1][j] + p;
			num = Math.floor(num / 10);
		}
		s = p.replace(/(.)*$/, "").replace(/^$/, "") + unit[0][i] + s;
	}

	return s
		.replace(/(.)*/, "")
		.replace(/(.)+/g, "")
		.replace(/^$/, "");
}

/* eslint no-useless-escape:0 */
const reg = /(((^https?:(?:\/\/)?)(?:[-;:&=\+\$,\w]+@)?[A-Za-z0-9.-]+(?::\d+)?|(?:www.|[-;:&=\+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[\+~%\/.\w-_]*)?\??(?:[-\+=&;%@.\w_]*)#?(?:[\w\?\=\&]*))?)$/;

export function isUrl(path) {
	return reg.test(path);
}
const startHttpReg = /^(http:|https:)[\w\?\&\.\#\%\*\@\$\!\~\/]*/;
export function isHttpStart(path) {
	return startHttpReg.test(path);
}

// routekey
export function formatterMapKey(data, mapKey = {}, parentPath = "/", notParPath = false) {
	// const notParPath = typeof parentPath == "boolean" && !parentPath;
	mapKey = Object.assign(
		{ iconClass: "iconClass", path: "path", name: "name", children: "children" },
		mapKey ? mapKey : {},
	);
	return data.map((item) => {
		let path = item[mapKey.path];
		if (!notParPath && !isHttpStart(path)) path = parentPath + path.replace(/^\/*/, "");
		const newData = {
			...item,
			iconClass: item[mapKey.iconClass] !== undefined ? item[mapKey.iconClass] : "smile-o",
			path,
			parPath: parentPath.replace(/\/$/, ""),
			name: item[mapKey.name],
		};
		if (Array.isArray(item[mapKey.children]) && item[mapKey.children].length) {
			newData[mapKey.children] = formatterMapKey(item[mapKey.children], mapKey, `${path}/`, notParPath);
		}
		return newData;
	});
}

// defaultConfigconfig
export const mergeConfig = (defaultConfig, theConfig) => {
	if (dataTypeTest(defaultConfig) === "object" && dataTypeTest(theConfig) === "object") {
		return merge(deepCopy(defaultConfig), theConfig);
	}
};

export function itemsFromTree({ tree, sourceItem, item, keyObj, action }) {
	let finished = false;
	keyObj = dataType.isObject(keyObj) ? keyObj : { id: "id", children: "children" };
	for (let index = 0; index < tree.length; index++) {
		const currentItem = tree[index];
		if (currentItem[keyObj.id] === sourceItem[keyObj.id]) {
			dataType.isFunction(action) && action({ tree, currentItem, item, index, keyObj });
			finished = true;
		} else if (Array.isArray(currentItem[keyObj.children])) {
			finished = itemsFromTree({ tree: currentItem[keyObj.children], sourceItem, item, keyObj, action });
		}
		if (finished) break;
	}
	return finished;
}
/**
 *json
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function removeItemFromTree(obj) {
	// { tree, sourceItem, keyObj }
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, index, keyObj }) {
		tree.splice(index, 1);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
/**
 *itemjsonsourceItem
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object,item:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function replaceItemFromTree(obj) {
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, item, index, keyObj }) {
		tree.splice(index, 1, item);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
/**
 *jsonsourceItemchildrenitem
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object,item:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function pushItemToTree(obj) {
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, item, index, keyObj }) {
		if (!Array.isArray(currentItem[keyObj.children])) {
			currentItem[keyObj.children] = [];
		}
		currentItem[keyObj.children].push(item);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
/**
 *jsonsourceItemchildrenitem
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object,item:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function unshiftItemToTree(obj) {
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, item, index, keyObj }) {
		if (!Array.isArray(currentItem[keyObj.children])) {
			currentItem[keyObj.children] = [];
		}
		currentItem[keyObj.children].unshift(item);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
/**
 *itemjsonsourceItem
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object,item:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function insertBeforeItemFromTree(obj) {
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, item, index, keyObj }) {
		tree.splice(index, 0, item);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
/**
 *itemjsonsourceItem
 *
 * @export
 * @param {object} obj { tree:array, sourceItem:object,item:object, keyObj:{id:"id",children:"children"} }
 * @returns
 */
export function insertAfterItemFromTree(obj) {
	const newobj = deepCopy(obj);
	newobj.action = function({ tree, currentItem, item, index, keyObj }) {
		tree.splice(index + 1, 0, item);
	};
	if (itemsFromTree(newobj)) {
		return newobj.tree;
	}
}
